import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import MainLayout from "layouts/MainLayout";

export default function App() {
  // This query will get all of your posts
  const postsValues = useStaticQuery(metaPostsQuery);
  const postObjects = postsValues.allMdx.edges
    .sort((a, b) => {
      const a_ = a.node.frontmatter.slug;
      const b_ = b.node.frontmatter.slug;
      return ("" + a_).localeCompare(b_);
    })
    .map((edge) => {
      const { title, date, slug, abstract, primary_thumb, secondary_thumb } =
        edge.node.frontmatter;
      const path = `/post/${slug}`;
      return (
        // <PostCard
        //   key={slug}
        //   link={path}
        //   title={title}
        //   date={date}
        //   abstract={abstract}
        //   primary={primary_thumb}
        //   secondary={secondary_thumb}
        // ></PostCard>
        <div>
          <p>{title}</p>
          <p>{abstract}</p>
          <p>{date}</p>
        </div>
      );
    });
  return (
    <MainLayout>{/* <Container fluid>{postObjects}</Container> */}</MainLayout>
  );
}

const metaPostsQuery = graphql`
  query {
    allMdx(filter: { fileAbsolutePath: { regex: "/posts/" } }) {
      edges {
        node {
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            slug
            abstract
            primary_thumb {
              childImageSharp {
                gatsbyImageData
              }
            }
            secondary_thumb {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
        }
      }
    }
  }
`;
